// Special ==================================================================================
.site-content {
    display: flex;
    margin: auto;
    margin-bottom: 5rem;
}

.navigation {
    display: none;
}

body {
    font-family: $font__2;
}

// Hero Banner==================================================================================
.home-hero {
    display: flex;
    align-items: center;
    flex: 1 1;
    background: linear-gradient(180deg, rgba(0, 0, 0, .4) 0, rgba(0, 0, 0, .4)), #808080 url(../../images/home-hero.jpg) no-repeat top;
    background-size: cover, cover;
    flex-direction: column;
    justify-content: center;
    margin: auto;
    height: 100vh;
}

p.site-description {
    display: none;
}

.site-header .container {
    display: flex;
    justify-content: center;
}

.main-navigation {
    display: flex;
    justify-content: flex-end;
    position: absolute;
}

h1.tent-logo {
    position: absolute;
    background: url(../../images/logos/inhabitent-logo-tent-white.svg) no-repeat;
    background-size: 50px 33px;
    font-size: 2em;
    left: 10px;
    top: -15px;
    width: 50px;
    height: 50px;
}

.main-navigation a {
    color: $color__text_light;
    padding: 1rem;
    text-transform: uppercase;
    font-family: $font__3;
    font-weight: lighter;
    &:hover {
    color: $color__text-screen;
}
}

.fade-in {
    position: fixed;
    z-index: 1000;
    h1.tent-logo {
        position: absolute;
        background: url(../../images/logos/inhabitent-logo-tent.svg) no-repeat;
        background-size: 50px 33px;
        font-size: 2em;
        left: 10px;
        top: -15px;
        width: 50px;
        height: 50px;
    }
    ul li a {
        color: $color__green;
        padding: 1rem;
        text-transform: uppercase;
        font-family: $font__3;
        font-weight: lighter;
        text-shadow: none;
    }
    ul li a:hover {
        color: $color__black;
    }
    fieldset .search-submit {
        display: inline;
        color: $color__green;
        font-size: 16px;
        line-height: 1;
        text-shadow: 0 1px 0 hsla(0, 0%, 100%, .8);
        cursor: pointer;
        :hover {
        color: $color__black;
        }
    }
}

.main-navigation.fade-in {
    background-color: hsla(0, 0%, 100%, .85);
    border-bottom: 1px solid $color__border-2;
    a:hover {
    color: $color__black;
    }
}
