// Latest Adventures ==============================================================================
.adventures-section {
    margin: 0 auto;
    width: 1280px;
    h2 {
        font-family: $font__3;
        font-weight: lighter;
        text-align: center;
        font-size: 38.5px;
    }
}

.adventures-container {
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 1em 1em;
    box-sizing: border-box;
    h3 {
        padding: 1rem;
    }
}

.strap:nth-child(1) { 
    grid-row: 1/3;
    grid-column: 1/3;
    height: 540px;
}
.strap:nth-child(2) { 
    margin-right: 2rem;
    grid-row: 1/3;
    grid-column: 3/5;
}
.strap:nth-child(3) { 
    margin-right: 2rem;
    grid-row: 2/3;
    grid-column: 3/4;
}
.strap:nth-child(4) { 
    margin-right: 2rem;
    grid-row: 2/3;
    grid-column: 4/5;
}

.canoe {
    margin: 0 auto;
    background: linear-gradient(180deg, rgba(0, 0, 0, .4), rgba(0, 0, 0, .4)), gray url(../../images/adventure-photos/canoe-girl.jpg) no-repeat top;
    background-size: cover;
    background-position: center;
    height: 575px;
    width: 540px;
    .text {
    color: $color__text_light;
    font-size: 54px;
    font-family: $font__2;
    font-weight: lighter;
    text-decoration: none;
    }
}

