// Begin About  ===================================================================================
.page-template-about {
    .entry-header {
        height: 100vh;
        display: flex;
        align-items: center;
        font-family: $font__3;
        font-size: 48px;
        text-transform: uppercase;
        flex: 1 1;
        color: $color__background-body;
        background-size: cover, cover;
        flex-direction: column;
        justify-content: center;
        margin: auto;
        height: 100vh;
        width: 100vw;
    }
    .entry-content {
        display: flex;
        justify-content: center;
        flex-direction: column;
        padding: 0 13.5rem 1rem 13.5rem;
        font-family: $font__2;
    }
    .entry-content h2 {
        text-align: center;
        font-family: $font__3;
        font-size: 34px;
        text-transform: uppercase;
    }
}

//  Shop Stuff ==============================================================================
.shopstuff {
    display: flex;
    height: 8rem;
    font-size: 38px;
    font-family: $font__3;
    justify-content: center;
    font-weight: lighter;
    text-transform: uppercase;
    padding: 2rem 0 2rem 0;
}

.product-info-container {
    display: flex;
    justify-content: center;
}

.product-type-blocks {
    display: flex;
    flex-direction: row;
    height: 60vh;
    width: 90vw;
    justify-content: space-around;
}

.product-type-block-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    width: 20vw;
    height: 50vh;
    border: 1px solid $color__border-2;
    border-top-width: 5px;
    max-width: 23.5%;
    padding: .75rem;
    img {
        height: 70px;
        width: 70px;
    }
}

.product-type-block-wrapper:hover {
    border: 1px solid $color__text-orange;
}

a.btn {
    display: flex;
    height: 40px;
    width: 140px;
    background-color: $color__green;
    border: 1px solid $color__green;
    margin: 1rem;
    font-size: 1rem;
    font-weight: lighter;
    color: $color__background-body;
    font-family: $font__3;
    align-self: auto;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    text-decoration: none;
}

a.btn:hover {
    background-color: #175751;
    color: $color__background-body;
    cursor: pointer;
}

.sdesc {
    display: flex;
    height: 1vh;
    width: 1vw;
}

//  Journal front Page ==============================================================================
.habj {
    display: flex;
    height: 5rem;
    font-family: $font__3;
    justify-content: center;
    font-weight: lighter;
    text-transform: uppercase;
}

.journal-container {
    display: flex;
    justify-content: center;
}

.journal {
    display: flex;
    flex-direction: row;
    height: 500px;
    width: 85vw;
    justify-content: space-around;
    justify-self: center;
}

.thumbs {
    display: flex;
    flex-direction: column;
    height: 430px;
    width: 27vw;
    border: 1px solid $color__background-hr;
    justify-content: flex-start;
    align-items: left;
    img {
        display: flex;
        height: 60vh;
        width: 40vw;
        border-bottom: 3px solid $color__background-hr;
    }
    a {
        text-decoration: none;
        color: black;
        border: 1px solid $color__black;
        padding: 0 1rem 0 1rem;
    }
    p {
        font-family: $font__2;
        text-align: left;
        height: 15px;
        margin: 1rem 0 0 0;
        color: $color__brand-grey;
        font-weight: lighter;
        font-size: 12px;
        align-self: left;
        padding-left: 1rem;
    }
}

div.jtitle {
    font-size: 21px;
    font-weight: bold;
    text-transform: uppercase;
    color: $color__brand-green;
    padding-left: 1rem;
}

.jbutton {
    display: flex;
    min-height: 40px;
    width: 140px;
    border: 1px solid $color__black;
    margin: 1rem;
    background-color: transparent;
    font-size: 1rem;
    font-weight: lighter;
    color: $color__black;
    font-family: $font__3;
    align-self: auto;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    text-decoration: none;
    &:visited{
    color: $color__black;
    }
}

.jbutton:hover {
    background-color: $color__green;
    border: 1px solid $color__green;
    color: $color__background-body;
    cursor: pointer;
}

// Journal Page ==================================================================================
.blog {
    .entry-header {
        display: flex;
        width: 70vw;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 20vh;
        img {
            width: 60vw; 
        }
        h2 {
            display: flex;
            align-self: left;
            background-color: $color__green;
            color: $color__background-body;
            font-size: 33.6px;
            font-size: 2.1rem;
            padding: .25rem 1.5rem .25rem 1.5rem;
            position: absolute;
            margin: 2.25rem 0 0 4rem;
            max-width: 40%;
            a {
                color: $color__background-body;
                text-transform: uppercase;
                text-decoration: none;
            }
        }
    }
    .entry-meta {
        width: 40vw;
        background-color: $color__green;
        color: $color__background-body;
        padding: .25rem 1.5rem;
        margin: 0 0 0 16rem;
    }
    .entry-content {
        flex-direction: column;
        padding: 0 3.5rem 0 3.5rem;
        font-family: $font__2;
    }
    div.jbtn {
        padding-left: 2.5rem;
    }
}

