        text-transform: uppercase;
        font-size: 28px;
    }
}

.form-submit {
    input.submit {
        background-color: $color__green;
        border: 1px solid $color__green;
        color: $color__background-body;
        font-size: 16px;
        text-transform: uppercase;
        cursor: pointer;
        border-radius: 0;
    }
}

.wpcf7-form-control.wpcf7-submit {
    background-color: $color__green;
    border: 1px solid $color__green;
    color: $color__background-body;
    font-size: 16px;
    text-transform: uppercase;
    cursor: pointer;
    border-radius: 0;
}

//  Single Shop Item =============================================================================
.whole {
    display: flex;
    flex-direction: row;
    padding: 0 0 6rem 0;
}

.single {
    img {
        padding: 5rem 0 0 5rem;
    }
    .wrap {
        display: flex;
        flex-direction: column;
        width: 40vw;
        padding: 3rem 0 0 2rem;
        h1 {
            text-transform: uppercase;
            color: $color__text-orange;
            font-size: 38px;
        }
        p {
            width: 40vw;
            padding: 0 0 0 1rem;
        }
        .price-meta {
            font-size: 20px;
            font-weight: bolder;
        }
        button {
            background: $color__background-body;
            border: 1px solid $color__black;
            color: $color__black;
            color: $color__black;
            cursor: pointer;
            font-family: $font__3;
            font-size: 14.4px;
            font-size: .9rem;
            line-height: 1;
            padding: .75rem 1.5rem;
            text-decoration: none;
            text-transform: uppercase;
            border-radius: 0;
        }
        button:hover {
            background-color: $color__green;
            border: 1px solid $color__green;
            color: $color__background-body;
            cursor: pointer;
        }
    }
}

//  Search ==========================================================================
fieldset {
    border: 0;
    margin: 0;
    padding: .7rem 1rem .4rem;
    .search-submit {
        display: inline;
        color: $color__background-body;
        font-size: 16px;
        line-height: 1;
        cursor: pointer;
         :hover {
            color: $color__text-screen;
        }
    }
    label {
        .search-field {
            width: 0px;
            transition: .8s;
        }
        &.expanded {
            input {
                width: 175px;
            }
        }
    }
    input.search-field {
        border: none;
        background: rgba(250, 250, 250, 0.0);
        color: $color__text-screen;
        outline: none;
        text-transform: uppercase;
        font-size: small;
        line-height: 2;
        &:focus {
            background: transparent;
        }
    }
}

.search-results {
    .site-main {
        width: 900px;
        height: 1050px;
        padding: 5rem 0 0 0;
        margin-bottom: 15rem;
        article {
            padding: 0 0 0 5rem;
        }
        h1 {
            text-transform: uppercase;
            border-bottom: 1px solid $color__border-2;
            margin-left: 5rem;
            padding: 0 0 1.5rem 0;
            font-size: 38px;
        }
        h2{
            font-size: 33px;
        }
        p {
            width: 50vw;
            padding: 0 5rem 0 0;
        }
        a {
            color: $color__green;
            line-height: 1;
            text-decoration: none;
            text-align: center;
            text-transform: uppercase;
        }
        .search-pagination {
            display: flex;
            border-top: 1px solid $color__border-2;
            font-family: Novecento Sans Web, sans-serif;
            padding: 1.5rem 0 0 3rem;
            text-transform: uppercase;
            a {
                background-color: $color__green;
                color: $color__text_light;
                display: inline-block;
                line-height: 1;
                text-decoration: none;
                padding: .5rem .75rem;
                margin-right: .25rem;
                text-align: center;
                font-size: 16px;
            }
            span {
                background-color: $color__brand-grey;
                color: $color__text_light;
                display: inline-block;
                line-height: 1;
                padding: .5rem .75rem;
                margin-right: .25rem;
                text-align: center;
            }
            p {
                width: 40vw;
                padding: 0 0 0 5rem;
            }
        }
        .jbtn {
            padding: 0 0 0 4rem;
        }
        span.dots {
            background-color: $color__green;
        }
    }
}

.search-no-results {
    .searchlogo {
        padding: 3rem 0 0 8rem;
    }
    .page-content {
        width: 70vw;
        height: 70vh;
    }
    fieldset label .search-field {
        width: 475px;
        background: $color__green;
    }
    p.sorry {
        padding: 0 0 0 5rem;
        font-size: 28px;
        width: 50%;
        text-align: center;
    }
}

//  Begin Sidebar =================================================================================
#secondary {
    width: 130vw;
}

.widget-area {
    display: flex;
    flex-direction: column;
    padding-left: 5%;
    width: 800px;
    padding: 3rem 0 0 0;
    border-left: 1px solid $color__border-2;
}

.widget {
    margin: 0 0 1.5rem;
    padding: 3rem 0 0 3rem;
}

.widget-title {
    font-family: $font__3;
    font-size: 24px;
    text-transform: uppercase;
}

.textwidget {
    font-family: $font__2;
}

.textwidget i {
    padding: 0 1rem 0 1rem;
}

.textwidget a {
    color: $color__green;
    font-family: $font__2;
    text-decoration: none;
}

.textwidget a:hover {
    color: $color__black;
}

.tent-biz-hours-class p {
    font-family: $font__2;
}

.dayweek {
    font-weight: bolder;
}

.widget_archive ul {
    list-style: none;
    font-family: $font__2;
    padding: 0;
    a {
        color: $color__green;
        text-decoration: none;
    }
}

// Archive ===================================================================================
.archive {
    .page-title {
        border-bottom: 1px solid $color__border-2;
        width: 90%;
        margin-left: 3rem;
    }
    h1 {
        font-size: 38px;
        text-transform: uppercase;
        font-family: $font__3;
        padding: 3rem 0 2rem 0;
    }
    .entry-header {
        display: flex;
        width: 70vw;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 20vh;
        img {
            width: 60vw;
        }
        h2 {
            display: flex;
            align-self: left;
            background-color: $color__green;
            color: $color__background-body;
            font-size: 33.6px;
            font-size: 2.1rem;
            padding: .25rem 1.5rem .25rem 1.5rem;
            position: absolute;
            margin: 2.25rem 0 0 4rem;
            max-width: 40%;
            a {
                color: $color__background-body;
                text-transform: uppercase;
                text-decoration: none;
            }
        }
    }
    .entry-meta {
        width: 40vw;
        background-color: $color__green;
        color: $color__background-body;
        padding: .25rem 1.5rem;
        margin: 0 0 0 16rem;
    }
    .entry-content {
        flex-direction: column;
        padding: 0 3.5rem 0 3.5rem;
        font-family: $font__2;
    }
    div.jbtn {
        padding-left: 2.5rem;
    }
}

.post-type-archive-product,
.tax-product-type {
    .site-main {
        display: flex;
        flex-flow: row wrap;
        justify-content: center;
        align-items: center;
    }
    .page-title {
        display: none;
    }
    .product-type-list {
        display: flex;
        font-family: $font__3;
        font-size: 19.2px;
        font-size: 1.2rem;
        justify-content: center;
        list-style-type: none;
        margin-left: 0;
        padding-left: 0;
        text-transform: uppercase;
        li a {
            text-decoration: none;
            padding-right: 3rem;
            color: $color__green;
        }
    }
    .page-header {
        display: block;
        width: 90%;
        text-align: center;
        border-bottom: 1px solid $color__brand-grey;
        margin-bottom: 5rem;
        padding-top: 5rem;
    }
    article {
        display: flex;
        flex-direction: column;
        width: 18rem;
        height: 18rem;
        margin: 0.5rem;
        border: 1px solid $color__background-pre;
        .entry-head {
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;
            height: auto;
        }
        img {
            flex: 4;
            border-bottom: 4px solid $color__brand-grey;
            min-height: 200px;
            max-height: 200px;
            &:hover {
            border-bottom: 0;
            }
        }
        .entry-container {
            display: flex;
            justify-content: center;
            align-items: center;
            flex: 1;
            a {
                color: black;
                text-decoration: none;
            }
            .entry-title {
                font-family: $font__2;
                font-size: 1rem;
                font-weight: normal;
            }
        }
        .entry-content {
            display: none;
        }
    }
}

.price-meta {
    display: flex;
    align-items: baseline;
    font-family: $font__2;
}

.price-meta::before {
    content: ".....$";
}

// Taxonomy ===================================================================================
.tax-product-type {
    article {
        height: 45vh;
    }
    img {
        height: 200px;
    }
    .entry-title {
        a {
            text-decoration: none;
            color: black;
        }
    }
}

// Find Us ===================================================================================
.post-16 {
    width: 64vw;
    margin: 5rem 0 0 5rem;
}
// 404 ===================================================================================
.error-404 {
    width: 64vw;
    margin: 5rem 0 0 5rem;
    font-family: $font__3;
    h1{
    text-transform: uppercase;    
    }
    p{
    font-family: $font__2;
    font-size: 16px;    
    }
}
.widget_recent_entries{
    padding: 0;
    ul {
    padding: 0;
    margin: 0;    
    text-decoration: none;
    color: $color__brand_green;
    list-style: none;
    a {   
    text-decoration: none;
    color: $color__brand_green;
    }
}
}
.widget-404 {
    ul {
    padding: 0;
    margin: 0;
    list-style: none;
    }
    a {   
    text-decoration: none;
    color: $color__brand_green;
    }
}
.search-submit-404 {
    height: 50px;
    width: 150px;
    background-color: $color__brand-green;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    text-decoration: none;
    margin: 0 0 0 1rem;
    color: $color__background-body;
    font-size: 20px;
    padding: .7rem;
    font-family: $font__3;
    font-weight: lighter;
    text-decoration: none;
    &:visited{
    color: $color__background-body;
    }
}
.search-submit-404:hover {
    background-color: $color__green;
    color: $color__background-body;
    cursor: pointer;
    font-size: 24px;
}


// Begin footer ===================================================================================
#colophon.site-footer {
    display: flex;
    background: url(../../images/dark-wood.png);
    background-size: 512px 512px;
    color: $color__background-body;

}

.footer-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    flex: 1 0 20%;
    width: $base__screen-width;
    font-family: $font__2;
    h3 {
        color: $color__text-orange;
        font-family: $font__3;
    }
    a {
        text-decoration: none;
        font-size: 10px;
        color: $color__brand-grey;
        align-self: center;
        padding-left: 1rem;
    }
}

.footer-image {
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
    margin: 0 0 0 6rem;
    width: 500px;
    height: 160px;
    img {
        width: auto;
        height: 50px;
    }
