.contact-info {
    margin: 2rem 0 0 3rem;
    p {
        display: flex;
        justify-content: flex-start;
        span {
            margin: .3rem;
        }
    }
}

.business-hours {
    margin: 2rem 0 1rem 3rem;
}

.copyright {
    align-self: flex-end;
    font-family: $font__3;
    margin: 2rem 0 2rem 0;
}
